.letter_input{
    margin: auto 20px;
}

.letter__td {
    border-bottom: 1px solid #000000;
    border-top: 1px solid #000000;
    padding: 5px;
}

.letter__td:first-child {
    border-left: 1px solid #000000;
}

.letter__td:nth-child(n+1) {
    border-right: 1px solid #000000;
}

.checkbox {
    width: 50px;
}

.title{
    margin: auto 20px;
    position: relative;
    z-index: 11;
    color: #000000;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    width: 170px;
    text-align: left;
}

.description{
    vertical-align: middle;
    text-align: left;
    line-height: 45px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    width: 800px;
}

.info {
    display: flex;
    align-items: center;
}